.main-footer {
  display: flex;
  flex-direction: column;
  background-color: $light-grey;
}

.main-footer__container {
  margin: 0 auto;
  width: 280px;

  @media (min-width: $tablet-width) {
    display: flex;
    align-items: center;
    margin: 0;
    width: 708px;
    padding: 63px 30px 60px 30px;
  }

  @media (min-width: $desktop-width) {
    padding: 57px 110px 58px 110px;
    width: 1220px;
  }
}

.main-footer__word-logo-link {
  display: block;
  padding: 0;
  padding-top: 39px;
  padding-bottom: 31px;
  text-align: center;

  @media (min-width: $tablet-width) {
    padding: 5px 0 5px 0;
    margin-right: 155px;
  }

  @media (min-width: $desktop-width) {
    margin-right: 413px;
  }
}

.main-footer__social {
  border-top: 1px solid #d9d9d9;
  border-bottom: 1px solid #d9d9d9;

  @media (min-width: $tablet-width) {
    margin-right: 129px;
  }

  @media (min-width: $desktop-width) {
    margin-right: 384px;
  }
}

.main-footer__copyright {
  display: flex;
  justify-content: space-between;
  align-content: center;
  padding-top: 22px;
  padding-bottom: 22px;

  @media (min-width: $tablet-width) {
    padding: 0;
    width: 163px;
  }
}

.main-footer__html-academy-link {
  display: block;
  align-self: center;
  font-size: 16px;
  line-height: 18px;
  color: $grey;
  text-decoration: none;
}

.main-footer__html-academy-logo-link {
  display: block;
}
